@import url(https://fonts.googleapis.com/css2?family=Italiana&family=Roboto:ital,wght@0,100;0,400;0,700;1,100;1,400;1,700&display=swap);
*, *::after, *::before{
	box-sizing: border-box;
}

a{
  text-decoration: none;
  color: inherit;
}

body{
	height: 300vh;
}

:root {
	--light-green: #dff6df;
	--green: #aae7aa;
	--gray: #adadad;
}

body {
	text-align: center;
	font-family: 'roboto';
}

h1, h2, h3, h4 {
	font-family: 'italiana';
}
.white-overlay::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(255, 255, 255, 0.25);
}
.white-overlay--dense::before {
	background-color: rgba(255, 255, 255, 0.56);
}

.input-container {
	position: relative;
}
.input {
	border: none;
	padding: 5px;
	border-radius: 8px;
	background-color: #eeeeee;
}
.input + label::after{
	display: block;
	content: "";
	height: 2px;
	width: 120%;
	background: linear-gradient(90deg, hsl(120, 56%, 62%) 0%, hsl(120, 76%, 29%) 100%);
	position: absolute;
	opacity: 0;
	-webkit-transform: scaleX(0);
	        transform: scaleX(0);
	-webkit-transform-origin: left;
	        transform-origin: left;
}
.input + label {
	opacity: .5;
	left: 5px;
	bottom: 5px;
	position: absolute;
	cursor: text;
}
.input + label, .input + label::after{
	transition: 0.3s cubic-bezier(0.23, 0.72, 0.28, 1.4);
	transition-property: opacity, -webkit-transform;
	transition-property: transform, opacity;
	transition-property: transform, opacity, -webkit-transform;
}

.input:focus + label{
	-webkit-transform: translateY(-135%) scale(.8);
	        transform: translateY(-135%) scale(.8);
	opacity: .8;
}
.input:hover + label::after, .input:active + label::after{
	opacity: .6;
	-webkit-transform: scaleX(.9);
	        transform: scaleX(.9);
}
.input:focus + label::after{
	-webkit-transform: translateY(-1.1em) scaleX(1);
	        transform: translateY(-1.1em) scaleX(1);
	opacity: 1;
}

