.wrapper{
	overflow: hidden;
	z-index: 2;
}
.wrapper__blur-background {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: red center var(--nav-background-pos-v);
	background: var(--nav-background, red) center var(--nav-background-pos-v);
	background-size: cover;
	/* background-attachment: fixed; */
	-webkit-filter: blur(4px);
	        filter: blur(4px);
	z-index: -1;
}
.cart__ico{
	padding: 10px;
	border-radius: 100%;
	overflow: visible;
	box-sizing: content-box;
	background-color: var(--light-green);
	margin-bottom: var(--price-margin);
}
.cart{
	/* position: absolute; */
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	--price-margin: 2px;
	font-size: .8em;
}
.cart>a{
	position: relative;
}
.cart__counter{
	position: absolute;
	background-color: yellow;
	vertical-align: middle;
	bottom: var(--price-margin);
	left: -5px;
	width: 1.5em;
	line-height: 1.5em;
	/* padding: 10px; */
	overflow: visible;
	border-radius: 100%;
	/* width: 30px; */
	/* height: 30px; */
	/* padding: 5px; */
}
.cart__price{
	font-size: .8em;
}
@-webkit-keyframes rotate {
	from {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotate {
	from {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.header {
	--nav-background: url(../../static/media/header_background.fbfe6524.jpg);
	--nav-background-pos-v: 53%;

	padding-top: 43px;
	background: red center var(--nav-background-pos-v);
	background: var(--nav-background, red) center var(--nav-background-pos-v);
	background-size: cover;
	width: 100%;
	height: 217px;
	position: relative;
}
.header__wrapper {
	height: 151px;
	position: relative;
}
.wrapper__nav.white-overlay::before {
	z-index: -1;
}
.wrapper__nav {
	margin: 10px 30px;
	height: 100%;
	display: grid;
	grid-template-columns: [start] 60px [left-mid] 1fr [right-mid] 60px [end];
	grid-template-rows: repeat(2, 1fr);
	justify-items: center;
	-webkit-align-items: center;
	        align-items: center;
}
.nav__logo {
	width: 75px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	grid-column: left-mid/right-mid;
}
.nav__input-container{
	grid-column: start/right-mid;
}
.nav__input-container > .input{
	width: 100%;
}

.logo__svg{
	overflow: visible;
}

/* animations */
.nav__logo g,
.nav__logo .leaves {
	-webkit-transform-origin: center;
	        transform-origin: center;
}
.nav__logo .outer-circle,
.nav__logo .inner-circle {
	transition: 0.8s opacity ease-out, 0.4s -webkit-transform ease-out;
	transition: 0.4s transform ease-out, 0.8s opacity ease-out;
	transition: 0.4s transform ease-out, 0.8s opacity ease-out, 0.4s -webkit-transform ease-out;
}
.nav__logo .text,
.nav__logo .leaves {
	-webkit-animation: 7s -3s rotate ease-in-out infinite paused;
	        animation: 7s -3s rotate ease-in-out infinite paused;
}
.nav__logo .text {
	animation-direction: alternate-reverse;
}
.nav__logo .outer-items {
	transition: 0.4s -webkit-transform ease-out;
	transition: 0.4s transform ease-out;
	transition: 0.4s transform ease-out, 0.4s -webkit-transform ease-out;
}
.nav__logo:hover .text,
.nav__logo:hover .leaves {
	-webkit-animation-play-state: running;
	        animation-play-state: running;
}
.nav__logo:hover .inner-circle {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	opacity: 0.7;
}
.nav__logo:hover .outer-items {
	-webkit-transform: scale(1.3);
	        transform: scale(1.3);
}
.nav__logo:hover .outer-circle {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	opacity: 0.4;
}

/* queries */
@media screen and (min-width: 1440px) {
	.header {
		height: 338px;
	}
	.header__wrapper {
		height: 200px;
	}
}
/* fix cover backgrounds mismatch on 315px breakpoint	 */
@media screen and (max-width: 315px) {
	.header__wrapper > .wrapper__blur-background {
		background-size: auto 143%;
	}
}

.nav__menu{
	position: relative;
}
.menu__list{
	position: absolute;
}
button.menu__burger{
	background: none;
	border: none;
}
