.wrapper{
	overflow: hidden;
	z-index: 2;
}
.wrapper__blur-background {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: var(--nav-background, red) center var(--nav-background-pos-v);
	background-size: cover;
	/* background-attachment: fixed; */
	filter: blur(4px);
	z-index: -1;
}