.cart__ico{
	padding: 10px;
	border-radius: 100%;
	overflow: visible;
	box-sizing: content-box;
	background-color: var(--light-green);
	margin-bottom: var(--price-margin);
}
.cart{
	/* position: absolute; */
	height: fit-content;
	--price-margin: 2px;
	font-size: .8em;
}
.cart>a{
	position: relative;
}
.cart__counter{
	position: absolute;
	background-color: yellow;
	vertical-align: middle;
	bottom: var(--price-margin);
	left: -5px;
	width: 1.5em;
	line-height: 1.5em;
	/* padding: 10px; */
	overflow: visible;
	border-radius: 100%;
	/* width: 30px; */
	/* height: 30px; */
	/* padding: 5px; */
}
.cart__price{
	font-size: .8em;
}