@keyframes rotate {
	from {
		transform: rotate(0);
	}
	to {
		transform: rotate(360deg);
	}
}

.header {
	--nav-background: url('../../assets/header_background.jpg');
	--nav-background-pos-v: 53%;

	padding-top: 43px;
	background: var(--nav-background, red) center var(--nav-background-pos-v);
	background-size: cover;
	width: 100%;
	height: 217px;
	position: relative;
}
.header__wrapper {
	height: 151px;
	position: relative;
}
.wrapper__nav.white-overlay::before {
	z-index: -1;
}
.wrapper__nav {
	margin: 10px 30px;
	height: 100%;
	display: grid;
	grid-template-columns: [start] 60px [left-mid] 1fr [right-mid] 60px [end];
	grid-template-rows: repeat(2, 1fr);
	justify-items: center;
	align-items: center;
}
.nav__logo {
	width: 75px;
	height: fit-content;
	grid-column: left-mid/right-mid;
}
.nav__input-container{
	grid-column: start/right-mid;
}
.nav__input-container > .input{
	width: 100%;
}

.logo__svg{
	overflow: visible;
}

/* animations */
.nav__logo g,
.nav__logo .leaves {
	transform-origin: center;
}
.nav__logo .outer-circle,
.nav__logo .inner-circle {
	transition: 0.4s transform ease-out, 0.8s opacity ease-out;
}
.nav__logo .text,
.nav__logo .leaves {
	animation: 7s -3s rotate ease-in-out infinite paused;
}
.nav__logo .text {
	animation-direction: alternate-reverse;
}
.nav__logo .outer-items {
	transition: 0.4s transform ease-out;
}
.nav__logo:hover .text,
.nav__logo:hover .leaves {
	animation-play-state: running;
}
.nav__logo:hover .inner-circle {
	transform: scale(1.1);
	opacity: 0.7;
}
.nav__logo:hover .outer-items {
	transform: scale(1.3);
}
.nav__logo:hover .outer-circle {
	transform: scale(1.2);
	opacity: 0.4;
}

/* queries */
@media screen and (min-width: 1440px) {
	.header {
		height: 338px;
	}
	.header__wrapper {
		height: 200px;
	}
}
/* fix cover backgrounds mismatch on 315px breakpoint	 */
@media screen and (max-width: 315px) {
	.header__wrapper > .wrapper__blur-background {
		background-size: auto 143%;
	}
}
